/******************************************************************
WORDPRESS LOGIN PAGE

This stylesheet is loaded on the WordPress login page

For more info, check out the codex:
http://codex.wordpress.org/Creating_Admin_Themes
******************************************************************/


body.login {}


#login {} /* the form box */


.login h1 a {
	background: url(../../assets/images/login-logo.png) no-repeat top center; 
	width: 320px;
	height: 120px;
	text-indent: -9999px;
	overflow: hidden;
	padding-bottom: 15px;
	display: block;
}


form {
	margin-left: 8px;
	padding: 26px 24px 46px;
	font-weight: normal;
	background: white;
	border: 1px solid #E5E5E5;
	border-radius: 3px;
	box-shadow: none;
}


body form .input {
	font-family: "HelveticaNeue-Light", "Helvetica Neue Light" ,"Helvetica Neue" ,sans-serif;
	font-weight: 200;
	font-size: 24px;
	width: 97%;
	padding: 3px;
	margin-top: 2px;
	margin-right: 6px;
	margin-bottom: 16px;
	border: 1px solid #E5E5E5;
	background: #FBFBFB;
	outline: none;
	box-shadow: inset 1px 1px 2px rgba(200,200,200,0.2);
}


#wp-submit {} /* login button*/